export const getNormalOption = (seriesData, basicSize) => {
    const axisLabel = {
        fontSize: basicSize,
    }
    // 饼图不需要设置x、y轴
    return {
        grid: {
            show: true, // 显示坐标系内的网格
            top: '17%',
            right: '10%',
            bottom: '10%',
            
            containLabel: true, 
        },
        title: {
            text: '标题根据组件接收的缩放参数缩放',
            left: 'center',
            top: basicSize * 0.3,
            textStyle: {
                fontSize: basicSize + 5,
                lineHeight: basicSize + 5,
            }
        },
        xAxis: {
            axisLabel
        },
        yAxis: {
            axisLabel
        },
        tooltip: {},
        series: [{
            name: '数据分布',
            type: 'scatter', // 类型为pie

            emphasis: {
                focus: 'series',
                itemStyle: {
                    borderColor: 'rgba(112, 131, 193, 0.5)',
                    borderWidth: basicSize
                }
            },
            data: seriesData
        }],
        // 数据轴缩放
        dataZoom: [{
            type: 'inside',
            start: 0,
            end: 100,
        },
        {
            type: 'slider',
            xAxisIndex: 0, // 默认值，第一条x轴
            show: true,
            start: 94,
            end: 100,
            // handleSize: 8,
            height: basicSize * 0.8,
            bottom: '5%'
          },]
    }
}