<template>
  <div ref="chart"></div>
</template>

<script>
import { props, inject, initMyChart, destroyed, computeAdjectSize, listenResize, removeListenResize } from '../commen'

import getOption from './option';
export default {
  name: 'chart-radat',
  props,
  inject,
  data() {
    return {

    }
  },
  watch: {
    viewSize: {
      deep: true,
      handler() {
        this.chart?.resize();
        // TODO：当屏幕太小，隐藏图例
      }
    },
    computeAdjectSize() {
      this.chart.setOption(this.getResizeOption())
    }
  },
  computed: {
    computeAdjectSize,
  },
  mounted: initMyChart,
  destroyed,
  methods: {
    getOption() {
      // bug: radar指定max后会报警告
      // The ticks may be not readable when set min: 0, max: 38000 and alignTicks: true
      const indicator = [
        { name: 'Sales', max1: 6500 },
        { name: 'Administration', max1: 16000 },
        { name: 'Information\nTechnology', max1: 30000 },
        { name: 'Customer\nSupport', max1: 38000 },
        { name: 'Development', max1: 52000 },
        { name: 'Marketing', max1: 25000 }
      ]
      const seriesData = [
        {
          value: [4200, 3000, 20000, 35000, 50000, 18000],
          name: 'Allocated Budget'
        },
        {
          value: [5000, 14000, 28000, 26000, 42000, 21000],
          name: 'Actual Spending'
        }
      ]
      console.log(this.computeAdjectSize);
      return getOption(indicator, seriesData, this.computeAdjectSize)
    },
    getResizeOption() {
      console.log(this.computeAdjectSize);
      return {
        title: {
          top: this.computeAdjectSize * 0.3,
          textStyle: {
            fontSize: this.computeAdjectSize + 5,
          }
        },
        radar: [{
          axisName: {
            fontSize: this.computeAdjectSize // 字体大小
          },
        }],
      }
    },
    listenResize,
    removeListenResize,
  },
}
</script>
<style scoped lang='scss'></style>