<template>
  <div ref="chart"></div>
</template>

<script>
import { props, inject, initMyChart, destroyed, computeAdjectSize, listenResize, removeListenResize } from '../commen'

import { /* normalOption as getOption, */ mutiLineOption } from './option';
export default {
  name: 'chart-pie',
  props,
  inject,
  watch: {
    viewSize: {
      deep: true,
      handler() {
        this.chart?.resize();
      }
    },
    computeAdjectSize() {
      this.chart.setOption(this.getResizeOption())
    }
  },
  computed: {
    computeAdjectSize,
  },
  mounted:initMyChart,
  destroyed,
  methods: {
    getOption() {
      const xData = ['Email', 'Union Ads', 'Video Ads', 'Direct', 'Search Engine'];
      // const yData = [120, 132, 101, 134, 90];
      const yDataList = [
        [120, 132, 101, 134, 390],
        [220, 182, 191, 234, 290]
      ]

      return mutiLineOption(xData, yDataList, this.computeAdjectSize)
    },
    getResizeOption() {
      const option = {
        title: {
          top: this.computeAdjectSize * 0.3,
          textStyle: {
            fontSize: this.computeAdjectSize + 5,
          }
        },
        xAxis: {
          axisLabel: {
            fontSize: this.computeAdjectSize,
          }
        },
        yAxis: {
          axisLabel: {
            fontSize: this.computeAdjectSize,
          }
        }
      }

      return option
    },
    listenResize,
    removeListenResize,
  },
}
</script>
<style scoped lang='scss'></style>