import * as echarts from 'echarts';
export const normalOption = (xData, yData, basicSize) => {

    // 饼图不需要设置x、y轴
    return {
        grid: {
            show: true, // 显示坐标系内的网格
            top: '17%',
            right: '10%',
            bottom: basicSize * 4,
            // containLabel: true, 
            backgroundColor: '#ffffffB3',
        },
        title: {
            text: '数据流',
            left: '4%',
            top: basicSize * 0.3,
            textStyle: {
                fontSize: basicSize + 5,
                lineHeight: basicSize + 5,
            }
        },
        tooltip: {},

        xAxis: {
            type: 'category',
            data: xData,
            axisLabel: {
                rotate: 20,
                fontSize: basicSize,
            }
        },
        yAxis: {
            type: 'value',
            axisLabel: {
                fontSize: basicSize
            }
        },
        series: [{
            // 常规折线图
            name: '网站数据',
            type: 'line', // 类型为折线图
            itemStyle: { // 给每个数据项添加样式
                shadowBlur: 80,
                shadowColor: 'rgba(0,0,0,.8)',
            },
            // 曲线图
            smooth: true, // 是否平滑曲线，否则为
            symbol: 'circle', // 拐点图形为圆形
            // 曲线图面积区域填充颜色
            areaStyle: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                    offset: 0,
                    color: 'rgba(0, 191, 255, 0.7)'
                },
                {
                    offset: 1,
                    color: 'rgba(0, 191, 255, 0.4)'
                }])
            },

            data: yData
        }]
    }
}

export const mutiLineOption = (xData, yDdatasList, basicSize) => {
    const dataSeries = yDdatasList.map((item, index) => {
        return {
            // 常规折线图
            name: '网站:' + index,
            type: 'line',
            areaStyle: {},
            emphasis: {
                label: {
                    show: true,  // 数据点处显示数值
                    position: 'top',
                   /*  textStyle: {
                        fontSize: basicSize + 1
                    } */
                },
                focus: 'series' // 悬停高亮当前的系列数据（某条折线的区域）
            },

            stack: 'total', // 所属堆叠的名称，相同的会进行优化，并且不会交叉重叠，例如线A即使有比B大的点，但A不会高于B
            data: item
        }
    })

    const option = normalOption(xData, null, basicSize)
    option.series = dataSeries
    return option;
}