<template>
  <div ref="chart"></div>
</template>

<script>
import { props, inject, initMyChart, destroyed, computeAdjectSize, listenResize, removeListenResize } from '../commen'

import getOption from './option';
export default {
  name: 'chart-pie',
  props,
  inject,
  data() {
    return {
      pieData: [],
    }
  },
  watch: {
    viewSize: {
      deep: true,
      handler() {
        this.chart?.resize();
        // TODO：当屏幕太小，隐藏图例
      }
    },
    computeAdjectSize() {
      this.chart.setOption(this.getResizeOption())
    }
  },
  computed: {
    computeAdjectSize,
  },
  mounted() {
    initMyChart.call(this);
    this.listenSeriesClick()
  },
  destroyed,
  methods: {
    async getOption() {
      const [err, data] = await this.getData();
      if (err) {
        this.$message('获取pie数据失败');
        return null
      }
      this.pieData = data;

      return getOption(this.pieData, this.computeAdjectSize)
    },
    getResizeOption() {
      return {
        title: {
          top: this.computeAdjectSize * 0.3,
          textStyle: {
            fontSize: this.computeAdjectSize + 5
          }
        },
        legend: {
          itemWidth: this.computeAdjectSize, // 设置图例项的宽度
          itemHeight: this.computeAdjectSize * .5, // 设置图例项的高度
          textStyle: {
            fontSize: this.computeAdjectSize - 2
          }
        },
        series: [{
          name: '网站数据',
          label: { // 每个数据区域对应的标题
            fontSize: this.computeAdjectSize,
          },
          labelLine: {
            show: true,
            length: this.computeAdjectSize * 1.3, // label线第一段的长度
            length2: this.computeAdjectSize * 1.1, // label线第二段的长度
          },
        }]
      }
    },
    getData() {
      this?.chart.showLoading();

      const url = process.env.NODE_ENV === 'production' ? '/dist/moke/test.json' : '/moke/test.json'
      return this.$axios.get(url).then(res => {
        return [null, res.pie]
      }).catch(err => {
        return [err, null]
      }).finally(() => {
        this?.chart.hideLoading();
      })
    },
    listenSeriesClick() {
      this.chart?.on('click', (params) => {
        this.$message(params.seriesName + ':' + params.name)
      })
    },
    listenResize,
    removeListenResize,
  },
}
</script>
<style scoped lang='scss'></style>