<template>
  <div ref="chart"></div>
</template>

<script>
import { props, inject, initMyChart, destroyed, computeAdjectSize, listenResize, removeListenResize } from '../commen'

import getOption from './option';
export default {
  name: 'chart-gauge',
  props,
  inject,
  watch: {
    viewSize: {
      deep: true,
      handler() {
        this.chart?.resize();
      }
    },
    computeAdjectSize() {
      this.chart.setOption(this.getResizeOption())
    }
  },
  computed: {
    computeAdjectSize,
  },
  mounted: initMyChart,
  destroyed,
  methods: {
    getOption() {
      const value = {
        first: 90,
        second: 60,
      }

      return getOption(value, this.computeAdjectSize)
    },
    getResizeOption() {
      const option = {
        title: {
          top: this.computeAdjectSize * 0.3,
          textStyle: {
            fontSize: this.computeAdjectSize + 5,
            lineHeight: this.computeAdjectSize + 5,
          }

        },
        series: [
          {
            name: 'second',
            detail: {
              rich: {
                value: {
                  fontSize: this.computeAdjectSize * 3,
                  padding: [0, 0, 0, this.computeAdjectSize]

                },
                unit: {
                  fontSize: this.computeAdjectSize * 1.5,
                  padding: [0, 0, -this.computeAdjectSize, 10]
                }
              }
            },

          },
          {
            name: 'first',
            detail: {
              rich: {
                value: {
                  fontSize: this.computeAdjectSize * 3,
                  padding: [0, 0, 0, this.computeAdjectSize]

                },
                unit: {
                  fontSize: this.computeAdjectSize * 1.5,
                  padding: [0, 0, -this.computeAdjectSize, 10]
                }
              }
            },

          },
        ]
      }

      return option
    },
    listenResize,
    removeListenResize,
  },
}
</script>
<style scoped lang='scss'></style>