export default (data, basicSize) => {
    /* data.forEach(element => {
        element.symbolSize = basicSize * 1.5;
    }); */
    // 饼图不需要设置x、y轴
    return {
        title: {
            text: '网站结构',
            left: '4%',
            top: basicSize * 0.3,
            textStyle: {
                fontSize: basicSize,
                lineHeight: basicSize,
            }
        },

        series: [
            {
                type: 'graph',
                name: 'websiteReation',
                data: data.items,
                links: data.links,
                layout: 'force',
                force: {
                    repulsion: basicSize * 8, // 斥力大小
                    edgeLength: basicSize * 8, // 边的两个端点的距离
                    qravity: 1, // 质量
                },
                symbolSize: basicSize * 1.5, // 圆球大小
                itemStyle: {
                    color: '#fc8452', // 圆球颜色
                },
                label: {
                    show: true,
                    position: 'bottom',
                    distance: 5, // label和圆球的距离
                    fontSize: basicSize * .9,
                },
                // 关系线配置
                lineStyle: {
                    opacity: 0.8,
                    width: basicSize * 0.2,
                    curveness: 0.1
                },
                edgeLabel: {
                    show: true,
                    fontSize: basicSize * 0.7,
                    position: 'middle',
                    formatter: function (x) {
                        return x.data.reation.name;
                    }
                },
                edgeSymbol: ['circle', 'arrow'],
                edgeSymbolSize: [basicSize * .2, basicSize * .6],
                roam: true,
                emphasis: {
                    focus: 'adjacency', // 悬停高亮当前，其它褪色
                }
            }
        ]
    };
}