
<template>
    <div>
        <el-input v-model="length" type="number"><template #prepend>个数：</template></el-input>
        <el-switch active-text="子项宽度固定" inactive-text="子项宽度不固定" v-model="isRegular" />
        <ul class="contain">
            <li class="item" v-for="(item, index) in arr" :[style]="{ 'width': (70 + 30 * Math.random()) + 'px' }"
                :key="index">{{
                    index + item }}</li>
            <!--  <i></i>
      <i></i>
      <i></i> -->
        </ul>
    </div>
</template>

<script lang="ts">
export default {
    name: 'test-flex',
    data() {
        return {
            isRegular: true,
            arr: [''],
            length: 1
        }
    },
    mounted() {

    },
    computed: {
        style() {
            return this.isRegular ? 'styles' : 'style'
        }
    },
    watch: {
        length() {
            this.arr.length = this.length
            this.arr.fill('d', 0, this.length)
        }
    },

    methods: {
        testPromise() {
            Promise.resolve()
                .then(() => { // p0
                    // 这里的代码块会注册为第一个微任务，此微任务执行后：log0，然后再注册一个微任务（logw所在代码块）
                    console.log(0);
                    return Promise.resolve(4).then(() => { // pw
                        // 这里的代码块会注册为log1后面的微任务，执行后：logw，
                        console.log('w');
                        return 4
                        // 执行后，注册一个微任务，来resolve P0
                    })
                })
                .then((r) => {
                    console.log(r);
                })


            Promise.resolve()
                .then(() => {
                    console.log(1);
                })
                .then(() => {
                    console.log(2);
                })
                .then(() => {
                    console.log(3);
                })
        }

    }
}
</script>
  
<style scoped>
/**
  最后一行左对齐：
  当父盒子宽度，列数固定，例如4列：
   
   margin-right：限制多，固定列和子元素宽度
    flex-satrt：
    直接使用flex-start左对齐排列，手动计算并设置子项的margin来间隔
    小技巧，:not(:nth-child(4n))选中除了4 8 12 ...等最后一列的元素，给margin-right，模拟space-between
    space-between：
    通过 :last-child:nth-child(4n - 1) 选中最后一个，且刚好在第三列，:last-child:nth-child(4n - 2)，最后一个，且在第二列，
    给它们设置计算好的margin-right即可
    
    例外：可以不固定列数
    最后一个元素 margin-right：auto
    让最后一行的剩余空间，都分配给最后一个元素的margin-right；添加一个伪元素设置felx：auto原理也是一样的。
    最终结果是最后一行的间距会和其它行不太一样，会破坏默认的剩余空间分配方式，全部空间都会优先放到最后一个元素上
  
  当父盒子宽度不固定，列数不固定
   末尾追加占位元素
    末尾追加元素，一般追加可能出现的最多列的个数减一即可。
    小技巧，当最多只有三列，此时只要最佳一个即可，可以给最后一个元素设置一个伪元素代替真实元素
  
  */
.contain {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    width: 500px;
    padding: 0;
    border: 2px solid brown;
    list-style: none;
}

.contain::after {
    content: '';
    flex: auto
}

/* .contain :last-child {
    margin-right: auto;
  } */
.item {
    margin: 10px;
}

.item {
    width: 100px;
    height: 100px;
    margin-bottom: 10px;
    border: 1px solid greenyellow;
    background-color: aquamarine;
}

/**给最后一个元素，组合在第几列，来设定margin-right */
/* .contain :last-child:nth-child(4n - 2) {
    margin-right: 239px;
  }
  .contain :last-child:nth-child(4n - 1) {
    margin-right: 119px;
  } */

/**给最后一行追加占位元素 */
.contain>i {
    width: 100px;
}
</style>
  