<template>
    <div class="layout">
        <div class="layout_tab">
            <el-tabs class="tab_meau" v-model="activeIndex" type="card" @tab-click="handleClick">
                <el-tab-pane v-for="(app, index) in allComponents" :key="app.name + index" :label="app.name ?? '组件' + index"
                    :name="String(index)"></el-tab-pane>
            </el-tabs>
            <div class="tab_toggle">
                <fullScreen></fullScreen>
            </div>

        </div>
        <div class="layout_content">
            <component :is="currentComponent"></component>
        </div>
    </div>
</template>
  
<script>
import { importComponents } from '@/utils';
import fullScreen from '@/components/tools/full-screen.vue';
export default {
    name: 'layout-con',
    data() {
        return {
            activeIndex: '0'
        }
    },
    components: {
        fullScreen
    },
    methods: {
        handleClick(tab) {
            this.activeIndex = tab.name
        }
    },

    computed: {
        allComponents() {
            return importComponents();
        },
        currentComponent() {
            return this.allComponents[this.activeIndex]
        }
    }
}
</script>
  
<style lang="scss">
// @import "@/style/variables.scss";
.layout {
    display: flex;
    width: 100%;
    height: 100%;
    padding: calc(1vmin);
    flex-direction: column;

    @at-root {
        .layout_tab {
            display: flex;
            flex-direction: row;

            @at-root {
                .tab_meau {
                    flex: 1;
                }

                .tab_toggle {
                    display: flex;
                    width: 130px;
                    height: 80%;
                    align-items: center;
                }
            }
        }

        .layout_content {
            flex: 1;
            overflow: auto;
        }
    }
}
</style>
  