<template>
    <div ref="chart"></div>
</template>

<script>
// import * as echarts from 'echarts';
import { getNormalOption as getOption } from './option'
import { props, inject, initMyChart, destroyed, computeAdjectSize, listenResize, removeListenResize } from '../commen'
export default {
    name: 'chart-scatter',
    props,
    inject,
    computed: {
        computeAdjectSize,
    },
    watch: {
        computeAdjectSize() {
            this.chart.setOption(this.getResizeOption())
        },
        viewSize: {
            handler() {
                if (this.$refs.chart === undefined) return;
                this.chart.resize();
            },
            deep: true
        }
    },
    mounted: initMyChart,
    destroyed: destroyed,
    methods: {
        getOption() {
            const seriesData = [
                [10.0, 8.04],
                [8.07, 6.95],
                [13.0, 7.58],
                [9.05, 8.81],
                [11.0, 8.33],
                [14.0, 7.66],
                [13.4, 6.81],
                [10.0, 6.33],
                [14.0, 8.96],
                [12.5, 6.82],
                [9.15, 7.2],
                [11.5, 7.2],
                [3.03, 4.23],
                [12.2, 7.83],
                [2.02, 4.47],
                [1.05, 3.33],
                [4.05, 4.96],
                [6.03, 7.24],
                [12.0, 6.26],
                [12.0, 8.84],
                [7.08, 5.82],
                [5.02, 5.68]
            ]
            return getOption(seriesData, this.computeAdjectSize)
        },
        getResizeOption() {
            const axisLabel = {
                axisLabel: {
                    fontSize: this.computeAdjectSize,
                }
            }
            return {
                title: {
                    top: this.computeAdjectSize * 0.3,
                    textStyle: {
                        fontSize: this.computeAdjectSize + 5,
                        lineHeight: this.computeAdjectSize + 5,
                    }
                },
                xAxis: axisLabel,
                yAxis: axisLabel
            }
        },
        listenResize,
        removeListenResize

    },
}
</script>
<style scoped lang='scss'></style>