<template>
    <div class="container">
        <div class="left">
            <div class="left_item">
                <pie :adject-width="30" :adject-height="30" class="full"></pie>
            </div>
            <div class="left_item">
                <bar :adject-width="30" class="full"></bar>
            </div>
            <div class="left_item">
                <lineBar :adject-width="30" :adject-height="20" class="full"></lineBar>
            </div>
        </div>
        <div class="center">
            <div class="center_item">
                <chinaMap :adject-width="40" :adject-height="20" class="full"></chinaMap>
            </div>
            <div class="center_item">
                <scatter :adject-width="40" :adject-height="20" class="full"></scatter>
            </div>
        </div>
        <div class="right">
            <div class="right_item">
                <graph :adject-width="30" class="full"></graph>
            </div>
            <div class="right_item" style="overflow: unset;">
                <radar :adject-width="20" class="full"></radar>
            </div>
            <div class="right_item">
                <gauge :adject-width="20" class="full"></gauge>
            </div>
        </div>

    </div>
</template>

<script>
import { throttle } from '@/utils/index';

import bar from '@/components/charts/chart-bar-study'
import pie from '@/components/charts/chart-pie'
import lineBar from '@/components/charts/chart-line'
import scatter from '@/components/charts/chart-scatter';
import radar from '@/components/charts/chart-radar';
import chinaMap from '@/components/charts/chart-map';
import gauge from '@/components/charts/chart-gauge';
import graph from '@/components/charts/chart-graph';

export default {
    name: 'charts-layout',
    provide() {
        return {
            viewSize: this.windowSize
        }
    },
    data() {
        return {
            windowSize: {
                width: null,
                height: null
            }
        }
    },
    beforeMount() {
        // 父组件的mouted在所有子组件都挂载完毕才会触发：即子组件的mounted会比父组件先触发
        // 但beforemounted会比所有子组件都先触发，类似于冒泡
        // 当子组件需要
        console.log('layout beforeMount');
        this.computeWindowSize()
        window.addEventListener('resize', this.resizeFn = throttle(() => {
            this.computeWindowSize();
        }, 150))
    },
    destroyed() {
        window.removeEventListener('resize', this.resizeFn)
    },
    components: {
        bar, pie, radar, graph, gauge, lineBar, scatter, chinaMap
    },
    methods: {
        computeWindowSize() {
            this.windowSize.width = document.documentElement.clientWidth
            this.windowSize.height = document.documentElement.clientHeight
        },
    },
}
</script>
<style scoped lang='scss'>
@import "@/style/function.scss";
$padding: 10px;

.full {
    width: 100%;
    height: 98%;
}

.container {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;
    // 垂直方向比较紧凑，echart实例的边缘label可能溢出（画到外边导致溢出）
    // overflow-y: hidden;
    background-color: bisque;
    padding: 0 $padding / 2 $padding;


    @at-root {

        .left,
        .center,
        .right {
            // 因为是横向是flex布局，会缩小
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            width: 30%;
            height: 100%;

        }

        .center {
            width: 40%;
        }

        .left_item,
        .center_item,
        .right_item {
            flex: 1 33%;
            // 高度一半
            height: 33%;
            padding: $padding $padding / 2 0px;
            background-color: rgb(222, 184, 135, .7);
            background-clip: content-box;
        }

        .center_item:first-child {
            flex: 1 60%;
            height: 60%;
        }

        .center_item:last-child {
            flex: 1 40%;
            height: 40%;
        }
    }
}
</style>