export default (indicator, data, basicFontSize) => {
    data.forEach((data) => {
        data.areaStyle = {
            // color: colors[index]
        }
        // label放在emphasis同级，则是普通时的样式，放到emphasis则是悬停时的样式，其它配置项也可以放进来
        data.emphasis = {
            label: {
                show: true,
                formatter: function (params) {
                    return params.value;
                }
            }
        }
    });
    return {
        title: {
            text: '运营数据',
            left: '4%',
            top: basicFontSize * 0.3,
            textStyle: {
                fontSize: basicFontSize + 5,
            }
        },
        tooltip: {
            trigger: 'item',
        },
        radar: [{
            shape: 'circle', // 默认多边形
            indicator, // 雷达图指示器：雷达图网格，指定多个指标维度
            axisName: {
                color: 'rgb(79, 77, 74)', // 指标字体颜色
                fontSize: basicFontSize || undefined // 字体大小
            },
            radius: '65%', // 雷达图大小
            center: ['55%', '50%'], // 雷达图位置
            startAngle: 60, // 起始角度，默认为 90 度，表示从右上方开始
            splitNumber: 5, // 雷达图分割段数
            splitArea: {
                areaStyle: {
                    color: ['rgba(222, 184, 135, 0.8)', 'rgba(222, 184, 135, 0.7)', 'rgba(222, 184, 135, 0.6)', 'rgba(222, 184, 135, 0.5)', 'rgba(222, 184, 135, 0.9)'],
                    shadowColor: 'rgba(0, 0, 0, 0.2)',
                    shadowBlur: 10
                }
            },

        }],
        series: [{
            name: 'Budget vs spending', // 雷达图名称
            type: 'radar', // 雷达图
            data: data // 雷达图数据
        }]
    }
}