/**
 * 导入components目录下的组件，并全部放到一个数组返回
 * @returns Array<components onject>
 */
export function importComponents() {
    const contexts = require.context('@/components/', false, /\.vue$/);
    const importAll = (resolve) => {
        let cache = {}
        resolve.keys().forEach(key => cache[key] = resolve(key))
        return cache;
    }
    const componentsMap = importAll(contexts);
    return Object.values(componentsMap).map(item => item.default);
}

export function throttle(fn, time) {
    let free = true;
    return (...args) => {
        if (free) {
            free = false;
            setTimeout(() => {
                fn.apply(null, args);
                free = true;
            }, time);
        }
    }
}

// Echarts图表字体、间距自适应
export const fitChartSize = (size, defalteWidth = 1920) => {
    let clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
    if (!clientWidth) return size;
    let scale = (clientWidth / defalteWidth);
    return Number((size * scale).toFixed(3));
}


// 函数防抖
export function debounce(fn, delay) {
    // 定义一个定时器，用于清除定时器
    var timer = null;
    // 返回一个函数，用于执行函数
    return function () {
        // 获取当前函数的上下文和参数
        var context = this;
        var args = arguments;
        // 清除定时器
        clearTimeout(timer);
        // 设置定时器，每次执行函数前，清除定时器
        timer = setTimeout(function () {
            fn.apply(context, args);
        }, delay);
    };
}

export function concatObject(obj1, obj2) {
    for (let key in Object.keys(obj2) ) {
        obj1[key] = obj2[key];
    }
    return obj1;
}

