export default (numData, scatterData, basicSize) => {

    return {
        // 渲染地图背景，相当于x轴和y轴
        geo: {
            // left、right、bottom、top设置地图倾斜
            left: 0,
            top: 0,
            right: 0,
            bottom: 0,
            type: 'map',
            map: 'chinaMap', // 需要提前注册
            roam: true, // 开启鼠标缩放和平移漫游
            zoom: 2, // 地图缩放比例
            center: [ // 中心点
                113.280637,
                23.125178
            ],
            itemStyle: {
                areaColor: 'rgba(248, 238, 225, 0.9)',
                shadowColor: 'rgba(145, 123, 96, 0.3)',
                shadowBlur: 20,
                opacity: .7
            },
            emphasis: {
                itemStyle: {
                    areaColor: 'rgba(222, 184, 135, 1)',
                    shadowColor: 'rgba(255, 255, 255, 0.5)',
                    shadowBlur: 20
                },
                label: {
                    show: true,
                    color: '#464646',
                    fontSize: 15,
                    fontWeight: 'bold',
                    offset: [0, -10],
                },

            }
        },
        visualMap: {
            left: 'right',
            min: 0,
            max: 50,
            seriesIndex: 0,

            inRange: {
                color: [
                    '#313695',
                    '#4575b4',
                    '#74add1',
                    '#abd9e9',
                    '#e0f3f8',
                    '#ffffbf',
                    '#fee090',
                    '#fdae61',
                    '#f46d43',
                    '#d73027',
                    '#a50026'
                ]
            },
            text: ['高', '低'],
            calculable: true
        },
        tooltip: {
            trigger: 'none', // 散点图没有设置tooltip不会触发
            showDelay: 0,
            transitionDuration: 0.2
        },
        series: [
            {
                name: '地区数据',// 系列名称
                type: 'map',//图表类型
                geoIndex: 0,
                itemStyle: {// 图形样式
                    normal: { label: { show: true } },// 默认状态下地图的文字
                    emphasis: { label: { show: true } }// 鼠标放到地图上面显示文字
                },
                tooltip: {
                    trigger: 'item', // 这里的tooltip会覆盖一层的tooltip
                },
                data: numData
            },
            {
                type: 'effectScatter', // 涟漪散点图
                name: 'effectScatter', // 涟漪散点图
                coordinateSystem: 'geo', // 地理坐标系中显示散点图
                symbolSize: basicSize * 0.5, // 涟漪散点图的大小
                // geoIndex: 0,
                rippleEffect: {
                    number: 2, // 每次涟漪层数
                    scale: 4 // 涟漪大小
                },
                color: '#fb8e6a',
                data: scatterData
            },
            {
                type: 'lines',
                name: 'lines',
                coordinateSystem: 'geo',
                zlevel: 1,
                effect: { // 箭头
                    show: true,
                    period: 5,
                    trailLength: 0,
                    symbol: 'arrow',
                    color: '#01AAED',
                    symbolSize: basicSize * 0.5,
                },
                lineStyle: { // 飞线
                    width: basicSize * 0.15,
                    opacity: 0.6,
                    curveness: -0.3, // 曲率
                    color: '#FFB800'
                },
                data: [{
                    coords: [
                        scatterData[0].value,   // 终点
                        scatterData[1].value,  // 起点
                    ]
                }]
            }
        ]
    }
}