<template>
  <div ref="bar"></div>
</template>

<script>
import * as echarts from 'echarts';
import { throttle } from '@/utils/index';
import getOption from './option'
export default {
  name: 'chart-bar-study',
  chart: null,
  props: {
    adjectWidth: {
      type: Number,
      default: 0
    },
    adjectHeight: {
      type: Number,
      default: 0
    },
  },
  inject: {
    viewSize: {
      from: 'viewSize',
      default: () => ({
        width: 0,
        height: 0
      })
    }
  },
  computed: {
    computeAdjectSize() {
      if (this.viewSize.width === 0 || this.viewSize.height === 0) {
        return 0
      }
      const result = this.adjectHeight * this.viewSize.height * 0.01 + this.adjectWidth * this.viewSize.width * 0.01;
      const divide = this.adjectHeight * this.adjectWidth === 0 ? 25 : 50; // 只要有一个是0，说明只依赖其中一个结果，否则是两个都加了，数值会更大
      return parseInt(result / divide) // result / 25 、12 + result / 125
    }
  },
  watch: {
    viewSize: {
      handler() {
        this.chart?.resize();
      },
      deep: true
    },
    computeAdjectSize() {
      this.chart?.setOption(this.getResizeOption())
    },
  },

  data() {
    return {
    }
  },
  mounted() {
    if (this.$refs.bar === undefined) return this.$message('error');
    this.chart = echarts.init(this.$refs.bar);
    this.chart.setOption(this.getOption());
    this.listenSeriesClick();
    this.listenResize();
  },
  destroyed() {
    this.removeListenResize()
    this.chart?.dispose?.();
  },
  methods: {
    getOption() {
      const xData = ['小红', '小明', '丽华', '乘胜', '武广'];
      const yData = [45, 44, 75, 39, 90]

      return getOption(xData, yData, this.computeAdjectSize)
    },
    getResizeOption() {
      return {
        title: {
          top: this.computeAdjectSize * 0.3,
          textStyle: {
            fontSize: this.computeAdjectSize + 1,
          }
        },
        xAxis: {
          axisLabel: {
            fontSize: this.computeAdjectSize,
          }
        },
        yAxis: {
          axisLabel: {
            fontSize: this.computeAdjectSize,
          }
        },
        series: {
          label: {
            textStyle: {
              fontSize: this.computeAdjectSize
            }
          }
        },
      }
    },
    resizeChart() {
      // this.getResizeOption();
      this.chart?.resize();
    },
    listenSeriesClick() {
      // series数组中第一组数据，且name是小明
      /* 
      {
        <mainType>Index: number // 组件 index
        <mainType>Name: string // 组件 name
        <mainType>Id: string // 组件 id
        dataIndex: number // 数据项 index
        name: string // 数据项 name
        dataType: string // 数据项 type，如关系图中的 'node', 'edge'
        element: string // 自定义系列中的 el 的 name
      } 
      */
      this.chart?.on('click', { seriesIndex: 0, name: '小明' }, (params) => {
        this.$message(params.name + '的' + params.seriesName + ': ' + params.value);
      })
    },
    listenResize() {
      !this.viewSize.width && window.addEventListener('resize', this.resizeFn = throttle(() => {
        console.log('self resize');
        this.chart?.resize();
      }, 150))
    },
    removeListenResize() {
      this.resizeFn && window.removeEventListener('resize', this.resizeFn);
    }
  },
}
</script>
<style scoped lang='scss'>
@import "@/style/function.scss";
</style>