<template>
  <div ref="chart"></div>
</template>

<script>
import { props, inject, initMyChart, destroyed, computeAdjectSize, listenResize, removeListenResize } from '../commen'

import getOption from './option';
export default {
  name: 'chart-graph',
  props,
  inject,
  watch: {
    viewSize: {
      deep: true,
      handler() {
        this.chart?.resize();
      }
    },
    computeAdjectSize() {
      this.chart.setOption(this.getResizeOption())
    }
  },
  computed: {
    computeAdjectSize,
  },
  mounted: initMyChart,
  destroyed,
  methods: {
    getOption() {
      const data = {
        items: [
          { name: '首页', id: 1 },
          { name: '登录页', id: 2 },
          { name: '关于页', id: 3 },
          { name: '个人中心', id: 4 },
          { name: '个人首页', id: 5 },
        ],
        links: [
          {
            source: '1', // 必须为字符串
            target: '2',
            reation: {
              name: '用户跳转',
              id: 1
            }
          },
          {
            source: '1',
            target: '3',
            reation: {
              name: '访客跳转',
              id: 2
            }
          },
          {
            source: '2',
            target: '4',
            reation: {
              name: '二级目录',
              id: 3
            }
          },
          {
            source: '2',
            target: '5',
            reation: {
              name: '二级目录',
              id: 4
            }
          },
        ]
      }

      return getOption(data, this.computeAdjectSize)
    },
    getResizeOption() {
      const option = {
        title: {
          top: this.computeAdjectSize * 0.3,
          textStyle: {
            fontSize: this.computeAdjectSize,
            lineHeight: this.computeAdjectSize,
          }
        },

        series: [
          {
            name: 'websiteReation',
            force: {
              repulsion: this.computeAdjectSize * 8, // 斥力大小
              edgeLength: this.computeAdjectSize * 8, // 边的两个端点的距离
            },
            symbolSize: this.computeAdjectSize * 1.5, // 圆球大小

            label: {
              fontSize: this.computeAdjectSize * .9,
            },
            // 关系线配置
            lineStyle: {
              width: this.computeAdjectSize * 0.2,
            },
            edgeLabel: {
              fontSize: this.computeAdjectSize * 0.7,
            },
            edgeSymbolSize: [this.computeAdjectSize * .2, this.computeAdjectSize * .6],
          }
        ]
      };

      return option
    },
    listenResize,
    removeListenResize,
  },
}
</script>
<style scoped lang='scss'></style>