// 注册地图，地图的option可以直接使用'chinaMap'注册
echarts.registerMap('chinaMap', chinaMap);


export const props = {
    adjectWidth: {
        type: Number,
        default: 0
    },
    adjectHeight: {
        type: Number,
        default: 0
    },
}

export const inject = {
    viewSize: {
        from: 'viewSize',
        default: () => ({
            width: 0,
            height: 0
        })
    }
}

/**
 * 计算当前屏幕像素，echart字体适应的大小基数
 * @returns 计算好的响应基数
 */
export function computeAdjectSize() {
    if (this.viewSize.width === 0 || this.viewSize.height === 0) {
        return 0
    }
    const result = this.adjectHeight * this.viewSize.height * 0.01 + this.adjectWidth * this.viewSize.width * 0.01;
    const divide = this.adjectHeight * this.adjectWidth === 0 ? 25 : 50; // 只要有一个是0，说明只依赖其中一个结果，否则是两个都加了，数值会更大
    return parseInt(result / divide) // result / 25 、12 + result / 125
}

/**
 * 获取dom中的echart容器，并初始化echart实例，挂到自身chart属性上
 * @returns void
 */
export async function initMyChart() {
    if (this.$refs.chart === undefined) return this.$message.error('初始化错误');
    this.chart = echarts.init(this.$refs.chart);
    // 防止echart高度计算异常
    requestAnimationFrame(async () => {
        this.chart.resize()
        // 初始化容器后即可调用原生的loading动画
        // this.chart.showLoading();
        // this.chart.hideLoading();
        const option = await this.getOption();
        if (option !== null) this.chart.setOption(option);
    })

    this.listenResize();
}

/**
 * 如果父组件没有提供响应变化的窗口数据，则清除自己监听的resize事件，并销毁echart
 */
export function destroyed() {
    this.removeListenResize();
    this.chart?.dispose?.();
}

/**
 * 监听窗口变化，调用chart实例的resize方法
 */
export function listenResize() {
    !this.viewSize.width && window.addEventListener('resize', this.resizeFn = throttle(() => {
        console.log('self resize');
        this.chart?.resize();
    }, 100))
}

/**
 * 清除掉组件监听的窗口变化监听，如果有的话
 */
export function removeListenResize() {
    this.resizeFn && window.removeEventListener('resize', this.resizeFn)
}

