export default (data, basicSize) => {
    // 每个数据上可以添加自己的itemStyle
    const colors = ['#51689b', '#ce5c5c', '#fbc357', '#8fbf8f', '#659d84', '#fb8e6a', '#c77288', '#786090', '#91c4c5', '#6890ba'];
    data.forEach((item, index) => {
        item.itemStyle = { color: colors[index] }
    });
    // 排序后，纳丁格尔饼图更好看
    data = data.sort(function (a, b) {
        return a.value - b.value;
    })

    // 饼图不需要设置x、y轴，grid也不生效，需要在series里的center设置圆心位置
    return {
        title: {
            text: '网站数据饼图',
            left: '4%',
            top: basicSize * 0.3,
            textStyle: {
                fontSize: basicSize + 5
            }
        },
        tooltip: {},
        legend: { // 作贴边，垂直显示，并居中
            left: 'left', // 贴左边
            top: 'middle', // 垂直居中
            orient: 'vertical',
            itemWidth: basicSize, // 设置图例项的宽度
            itemHeight: basicSize * .5, // 设置图例项的高度
            textStyle: {
                fontSize: basicSize - 2
            }
        },
        series: [{
            name: '网站数据',
            type: 'pie', // 类型为pie
            radius: ['30%', '58%'], // 内圈半径，外圈半径。不设置则是完整饼图
            center: ['55%', '60%'], // 饼图位置
            label: { // 每个数据区域对应的标题
                fontSize: basicSize,
                // position: 'outsize', // 每个数据块标题位置：outsize：一根线指着外边的标题，inside：数据块上，不使用线指向外边，center：中心显示，只显示选中的
                position: 'outer', // 有label线，outsize没有线
                // alignTo: 'labelLine',
                // bleedMargin: 5, // 饼图上label和数据块的距离
            },
            labelLine: {
                show: true,
                length: basicSize * 1.3, // label线第一段的长度
                length2: basicSize * 1.1, // label线第二段的长度
            },

            /* 饼图位置  
             left: '33.3333%',
             right: '33.3333%',
             top: 0,
             bottom: 0, */
            roseType: 'radius', // 使用纳丁格尔饼图，值为具体的类型
            itemStyle: { // 给每个数据项的区域添加样式
                shadowBlur: basicSize * 3,
                shadowColor: 'rgba(0,0,0,.8)'
            },
            data: data
        }]
    }
}