<template>
    <div>
        <el-switch v-model="isFullScreen" active-text="全屏" inactive-text="非全屏" @change="toggleFullScreen">
        </el-switch>
    </div>
</template>

<script>
export default {
    name: 'full-screen',
    data() {
        return {
            isFullScreen: false
        }
    },
    methods: {
        async toggleFullScreen() {
            if (!document.fullscreenElement) {
                const [ok] = await document.documentElement.requestFullscreen().then((resolve) => {
                    return [resolve, null];
                }).catch((err) => {
                    return [null, err];
                });
                if (ok) {
                    this.isFullScreen = true;
                }
                return
            }
            if (document.exitFullscreen) {
                document.exitFullscreen();
                this.isFullScreen = false;
            }
        }
    },
}
</script>
<style scoped lang='scss'></style>