export default (xData, yData, basicFontSize) => ({
    title: {
        text: '用户积分',
        left: '4%',
        top: basicFontSize * 0.3,
        textStyle: {
            fontSize: basicFontSize + 1,
        }
    },
    grid: {
        left: '3%',
        right: '10%',
        bottom: '5%',
        containLabel: true
    },
    xAxis: {
        // 坐标轴类型：value：数据轴，category：类目轴。
        // xAxis 与 yAxis的值互换即可切换横向柱状图
        type: 'category',
        data: xData,
        axisLabel: {
            fontSize: basicFontSize,
        }
    },
    yAxis: {
        type: 'value', // series的所有系列数据都会限制在这个轴上
        axisLabel: {
            fontSize: basicFontSize,
        }
    },
    tooltip: {},
    series: [ // 系列，数据轴显示多少类图表
        {
            data: yData,
            name: '积分', // 本系列数据类目轴标题(tooltip的标题)，用于区分多组数据
            type: 'bar', // 数据的展现类型
            // barWidth: basicFontSize*1.5, // 指定了type为bar，barWidth才有意义：柱状图每个数据柱的宽度
            color: '#345', // 每个数据柱的颜色
            itemStyle: { // 单个轴的颜色
                color(params) {
                    return '#23' + params.dataIndex // 每个柱子不同颜色
                }
            },
            markPoint: { // 对某个数据进行标注，如最大值最小值
                data: [
                    {
                        type: 'max',
                        name: '最多' // tooltip显示的名称
                    },
                    {
                        type: 'min',
                        name: '最少'// tooltip显示的名称
                    },
                ]
            },
            markLine: { // 对本组数据进行标线，如平均线
                data: [
                    {
                        type: "average",
                        name: '平均数'// tooltip显示的名称
                    }
                ]

            },
            label: { // 柱子上的数值
                show: true,
                position: 'inside', // 数值显示的位置
                formatter: '{c}个',
                textStyle: {
                    fontSize: basicFontSize
                }
            },
        },
        {
            name: '折线图',
            type: 'line',
            data: yData
        }
    ]
})