<template>
  <div ref="chart"></div>
</template>

<script>
import { props, inject, initMyChart, destroyed, computeAdjectSize, listenResize, removeListenResize } from '../commen'

import getOption from './option';
export default {
  name: 'chart-map',
  props,
  inject,
  data() {
    return {

    }
  },
  watch: {
    viewSize: {
      deep: true,
      handler() {
        this.chart?.resize();
        // TODO：当屏幕太小，隐藏图例
      }
    },
    computeAdjectSize() {
      this.chart.setOption(this.getResizeOption())
    }
  },
  computed: {
    computeAdjectSize,
  },
  mounted: initMyChart,
  destroyed,
  methods: {
    getOption() {
      const numData = [
        { name: '北京市', value: 1 },
        { name: '天津市', value: 2 },
        { name: '上海市', value: 3 },
        { name: '重庆市', value: 4 },
        { name: '河北省', value: 5 },
        { name: '河南省', value: 6 },
        { name: '云南省', value: 7 },
        { name: '辽宁省', value: 8 },
        { name: '黑龙江省', value: 9 },
        { name: '湖南省', value: 10 },
        { name: '安徽省', value: 11 },
        { name: '山东省', value: 12 },
        { name: '新疆维吾尔自治区', value: 13 },
        { name: '江苏省', value: 14 },
        { name: '浙江省', value: 15 },
        { name: '江西省', value: 16 },
        { name: '湖北省', value: 17 },
        { name: '广西壮族自治区', value: 18 },
        { name: '甘肃省', value: 19 },
        { name: '山西省', value: 20 },
        { name: '内蒙古自治区', value: 21 },
        { name: '陕西省', value: 22 },
        { name: '吉林省', value: 23 },
        { name: '福建省', value: 24 },
        { name: '贵州省', value: 25 },
        { name: '广东省', value: 26 },
        { name: '青海省', value: 27 },
        { name: '西藏自治区', value: 28 },
        { name: '四川省', value: 29 },
        { name: '宁夏回族自治区', value: 30 },
        { name: '海南省', value: 31 },
        { name: '台湾省', value: 32 },
        { name: '香港省', value: 33 },
        { name: '澳门省', value: 34 }
      ]
      const scatterData = [
        { name: '广东省', value: [113.23, 23.16, 1] },
        { name: '重庆市', value: [106.54, 29.59, 1] },
      ]
      return getOption(numData, scatterData, this.computeAdjectSize)
    },
    getResizeOption() {
      return {
        series: [
            {
                name: 'effectScatter', // 涟漪散点图
                symbolSize: this.computeAdjectSize * 0.5, // 涟漪散点图的大小
            },
            {
                name: 'lines',
                effect: { // 箭头
                    symbolSize: this.computeAdjectSize * 0.5,
                },
                lineStyle: { // 飞线
                    width: this.computeAdjectSize * 0.15,
                },
            }
        ]
      }
    },
    listenResize,
    removeListenResize,
  },
}
</script>
<style scoped lang='scss'></style>