export default (data, basicSize) => {
    // 饼图不需要设置x、y轴
    return {
        title: {
            text: '访问数据',
            left: '4%',
            top: basicSize * 0.3,
            textStyle: {
                fontSize: basicSize + 5,
                lineHeight: basicSize + 5,
            }
        },

        series: [
            {
                name: 'second',
                type: 'gauge',
                startAngle: 180,
                endAngle: 0,
                min: 0,
                max: 100,
                progress: {
                    show: true,
                    overlap: false,
                    roundCap: true,
                    clip: false,
                    itemStyle: {
                        borderColor: '#464646'
                    }
                },
                pointer: {
                    show: false
                },
                splitLine: {
                    show: false,
                    distance: 0,
                    length: 10
                },
                axisTick: {
                    show: false
                },
                axisLabel: {
                    show: false,
                    distance: 50
                },
                title: {
                    offsetCenter: ['0%', '-30%']
                },
                detail: {
                    offsetCenter: [0, '30%'],
                    valueAnimation: true,
                    formatter: function (value) {
                        return '{value|' + value + '}{unit|%}';
                    },
                    rich: {
                        value: {
                            fontSize: basicSize * 3,
                            fontWeight: 'bolder',
                            color: '#464646',
                            padding: [0, 0, 0, basicSize]

                        },
                        unit: {
                            fontSize: basicSize * 1.5,
                            color: '#777',
                            padding: [0, 0, -basicSize, 10]
                        }
                    }
                },
                center: ['26%', '65%'],
                radius: '60%',
                data: [
                    {
                        value: data.second,
                        name: '二次访问率'
                    }
                ]
            },
            {
                name: 'first',
                type: 'gauge',
                startAngle: 180,
                endAngle: 0,
                min: 0,
                max: 100,
                progress: {
                    show: true,
                    overlap: false,
                    roundCap: true,
                    clip: false,
                    itemStyle: {
                        borderColor: '#464646'
                    }
                },
                pointer: {
                    show: false
                },
                splitLine: {
                    show: false,
                    distance: 0,
                    length: 10
                },
                axisTick: {
                    show: false
                },
                axisLabel: {
                    show: false,
                    distance: 50
                },
                title: {
                    offsetCenter: ['0%', '-30%']
                },
                detail: {
                    offsetCenter: [0, '30%'],
                    valueAnimation: true,
                    formatter: function (value) {
                        return '{value|' + value + '}{unit|%}';
                    },
                    rich: {
                        value: {
                            fontSize: basicSize * 3,
                            fontWeight: 'bolder',
                            color: '#464646',
                            padding: [0, 0, 0, basicSize]

                        },
                        unit: {
                            fontSize: basicSize * 1.5,
                            color: '#777',
                            padding: [0, 0, -basicSize, 10]
                        }
                    }
                },
                center: ['74%', '65%'],
                radius: '60%',
                data: [
                    {
                        value: data.first,
                        name: '首次点击率'
                    }
                ]
            },
        ]
    };
}